  padding-bottom: 40px; }
  @media (min-width: 660px) {
    .site-form__info {
      order: 1;
      padding-bottom: 0;
      padding-top: 68px; } }
  @media (min-width: 960px) {
    .site-form__info {
      width: 50%;
      order: 1;
      padding-right: 35px;
      padding-left: 0;
      padding-top: 73px; } }

.site-form__achievements {
  padding: 35px 20px;
  padding-bottom: 9px;
  padding-top: 74px; }
  @media (min-width: 660px) {
    .site-form__achievements {
      order: 4;
      padding-top: 23px; } }
  @media (min-width: 960px) {
    .site-form__achievements {
      width: 940px;
      padding: 0;
      margin-top: 60px; } }
  .site-form__achievements--grey {
    background-color: #f2f2f2; }
    @media (min-width: 660px) {
      .site-form__achievements--grey {
        background-color: #ffffff; } }

.site-form__contacts {
  padding: 40px 20px;
  padding-top: 16px; }
  @media (min-width: 660px) {
    .site-form__contacts {
      padding-left: 20px;
      padding-right: 20px;
      order: 3;
      padding-top: 55px; } }
  @media (min-width: 960px) {
    .site-form__contacts {
      width: 940px;
      padding: 0;
      order: 3;
      margin-top: 56px; } }

.site-form__os {
  padding: 74px 20px;
  padding-bottom: 10px; }
  @media (min-width: 660px) {
    .site-form__os {
      padding-left: 20px;
      padding-right: 20px;
      padding-top: 70px;
      order: 2; } }
  @media (min-width: 960px) {
    .site-form__os {
      width: 50%;
      padding-left: 35px;
      padding-right: 0; } }
  .site-form__os--grey {
    background-color: #f2f2f2; }
    @media (min-width: 660px) {
      .site-form__os--grey {
        background-color: #ffffff; } }

.site-form__review {
  padding: 50px 20px;
  padding-bottom: 48px; }
  @media (min-width: 660px) {
    .site-form__review {
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 66px;
      padding-top: 55px;
      order: 5; } }
  @media (min-width: 960px) {
    .site-form__review {
      width: 940px;
      padding: 0;
      margin-top: 60px; } }

.site-form__button {
  padding-left: 20px;
  padding-right: 20px; }
  @media (min-width: 660px) {
    .site-form__button {
      display: flex;
      order: 6;
      padding-bottom: 50px; } }
  @media (min-width: 960px) {
    .site-form__button {
      justify-content: space-between;
      width: 940px;
      margin-top: 64px;
      padding-right: 0;
      padding-left: 0;
      padding-bottom: 0; } }

.popup {
  display: none;
  text-align: center;
  padding-top: 40px;
  margin: 0 auto;
  box-shadow: 0 18px 8px -4px rgba(0, 0, 0, 0.1); }
  @media (min-width: 660px) {
    .popup {
      width: 460px;
      padding-top: 40px; } }
  @media (min-width: 960px) {
    .popup {
      width: 780px; } }
  .popup h3 {
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;
    margin: 0; }

@media (min-width: 660px) {
  .popup-text {
    padding-left: 70px;
    padding-right: 70px; } }

@media (min-width: 960px) {
  .popup-text {
    padding-left: 250px;
    padding-right: 250px;
    padding-bottom: 50px;
    font-size: 18px; } }

@media (min-width: 960px) {
  .popup-text--successfully {
    margin: 0;
    padding-top: 20px;
    padding-left: 200px;
    padding-right: 200px;
    padding-bottom: 50px; } }

.popup__footer {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 50px;
  padding-bottom: 50px; }
  .popup__footer--successfully {
    background-color: #f2f2f2; }
  @media (min-width: 960px) {
    .popup__footer--fail {
      background-color: #f2f2f2; } }
